body {
  min-height: 100vh;
  min-width: 100vw;
  background-color: #fff;
  margin: 0;
  padding: 0;
}

.blocks-list {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  list-style: none;
}

.block-item {
  position: relative;
}

.block-index {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-size: 14px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

